@import "../../global.scss";



.contact{
    background-color: white;
    display: flex;

    @include mobile{
        flex-direction: column;
        
    }

    @media only screen and (max-width: 600px) {
        flex-direction: column;
        align-items: center;

    } 


    .left{

        flex: 1;
        overflow: hidden;
        &.img{
            height: 100%;

            @media only screen and (max-width: 600px) {
                width: 100%;
            } 
            
        }
    }

    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        h2{
            font-size: 30px;
        }

        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;

            input{
                width: 300px;
                height: 30px;
                font-size: 14px;

                @include mobile{
                    width: 200px;
                    height: 20px;
                    
                }

                @media only screen and (max-width: 600px) {
                    width: 200px;
                    height: 20px;
                } 
            }

            textarea{
                width: 300px;
                height: 200px;
                font-size: 14px;

                @include mobile{
                    width: 200px;
                    height: 100px;
                    
                }

                @media only screen and (max-width: 600px) {
                    width: 200px;
                    height: 100px;
                } 
            }

            button{
                width: 150px;
                height: 30px;
                color: white;
                background-color: #B89439;
                border: none;
                border-radius: 10px;
                font-weight: 500;
                cursor: pointer;

                &:focus{
                    outline: none;
                }

            }

            span{
                color: #B89439;
                font-weight: bold;
            }
        }

    }




}